@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-v20-latin_cyrillic-regular.woff2") format("woff2");
  src: url("../fonts/roboto-v20-latin_cyrillic-regular.woff") format("woff");
  font-weight: normal;
  font-display: swap;
  font-style: normal;
  unicode-range: u+0030-0039, u+0400-045f, u+0490-0491, u+04b0-04b1, u+2116;
}

