* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-width: 1160px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  color: #1f1e25;
}

.body-hidden {
  overflow-y: hidden;
}

.section h1 {
  font-size: 54px;
  line-height: 54px;
  margin-bottom: 15px;
  font-weight: 700;
}

.section h2 {
  font-size: 41px;
  font-weight: 700;
  margin-bottom: 55px;
}

.section h3 {
  font-weight: 500;
  font-size: 22px;
}

@media (max-width: 1199px) {
  body {
    min-width: 678px;
  }
  html,
  body {
    overflow-x: hidden;
  }
  .section h1 {
    font-size: 46px;
    margin-bottom: 8px;
  }
  .section h2 {
    font-size: 32px;
    margin-bottom: 33px;
  }
  .section h3 {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  body {
    min-width: 290px;
  }
  .section h1 {
    font-size: 38px;
    margin-bottom: 4px;
  }
  .section h2 {
    font-size: 22px;
    margin-bottom: 25px;
  }
  .section h3 {
    font-size: 16px;
  }
}
.container {
  position: relative;
  width: 1170px;
  margin: 0 auto;
  z-index: 2;
}

.container__flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 1199px) {
  .container {
    width: 678px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 290px;
  }
}
.list {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  border: 0;
  position: relative;
  vertical-align: bottom;
}

.button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* stylelint-disable-next-line */
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  /* stylelint-disable-next-line */
  -moz-appearance: textfield;
}

input[type=submit] {
  /* stylelint-disable-next-line */
  -webkit-appearance: none;
}

