  text-align: center;
}

.popup__thanks_active {
  display: block;
}

.popup__close_thanks {
  position: absolute;
  top: 25px;
  right: 25px;
}

@media (max-width: 1199px) {
  .popup__thanks {
    width: 100%;
  }
  .popup__close_thanks {
    top: 30px;
    right: 30px;
  }
  .popup__text-main_thanks {
    max-width: none;
  }
  .popup__text-below_thanks {
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .popup__thanks {
    padding: 40px 23px;
  }
  .popup__close_thanks {
    top: 15px;
    right: 15px;
  }
}
